import React from "react";
import axios from "axios";
import { useQuery } from "@tanstack/react-query";
import { Box, Button, Grid, Paper, Typography } from "@mui/material";

export default function GetProfile(){

    const {isLoading, error, data, isFetching, refetch} = useQuery({
        queryKey: ['character'],
        queryFn: () =>
        axios.get(`https://rickandmortyapi.com/api/character/${Math.floor(Math.random()*21)}`)
        .then((res) => res.data)
    });

    if (isLoading) return "Loading...";

    if (error) return "An error has occured: " + error.message;

    return(
        <Box>
        <Grid container spacing={1} sx={{ display: "flex", flexDirection: 'row', padding: 1 }}>
          
          <Grid item xs={12}>
            <Paper elevation={0} sx={{}}>
              <Typography sx={{padding:1}}><Box component='img' sx={{borderRadius: '100%'}} src={data.image}/></Typography>
            </Paper>
          </Grid>
          
        </Grid>
  
              
       <Grid item xs={12} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center', color: '#1854ba'}}><Button variant='outlined' onClick={refetch}>Refetch</Button></Grid> 
      </Box>
    );

}