import React from "react";
import axios from "axios";
import { useQuery } from "@tanstack/react-query";
import { Box, Button, Grid, Paper, Typography } from "@mui/material";

export default function GetCharacters(){

    const {isLoading, error, data, isFetching, refetch} = useQuery({
        queryKey: ['character'],
        queryFn: () =>
        axios.get(`https://rickandmortyapi.com/api/character/${Math.floor(Math.random()*21)}`)
        .then((res) => res.data)
    });

    if (isLoading) return "Loading...";

    if (error) return "An error has occured: " + error.message;

    return(
        <Box component="span">
        <Grid container spacing={1} sx={{ display: "flex", padding: 1 }}>
          <Grid item xs={12}>
            <Paper>
              <Typography sx={{padding:1 , color: '#616975'}}>Name: <i>{data.name}</i></Typography>
            </Paper>
          </Grid>
          <Grid item xs={12}>
            <Paper>
              <Typography sx={{padding:1 , color: '#616975'}}>Origin: {data.origin.name}</Typography>
            </Paper>
          </Grid>
          <Grid item xs={12}>
            <Paper>
              <Typography sx={{padding:1 , color: '#616975'}}>Species: {data.species}</Typography>
            </Paper>
          </Grid>
        </Grid>
  
      </Box>
    );

}