import React from "react";
import axios from "axios";
import { useQuery } from "@tanstack/react-query";
import { Box } from "@mui/system";
import { Button, Divider, Grid, Paper, Typography } from "@mui/material";

export default function GetQuotes() {
  const { isLoading, error, data, isFetching, refetch } = useQuery({
    queryKey: ["quotes"],
    queryFn: () =>
      axios.get("https://api.quotable.io/random").then((res) => res.data),
  });

  if (isLoading) return "Loading...";

  if (error) return "An error has occured: " + error.message;

  return (
    <Box>
      <Grid container spacing={1} sx={{ display: "flex", padding: 1 }}>
        <Grid item xs={12}>
          <Paper>
            <Typography sx={{padding:1, color: '#616975'}}>"{data.content}"</Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} >
          <Paper>
            <Typography sx={{padding:1, color: '#616975'}}>Author: <i>{data.author}</i></Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} paddingBottom="10px">
          <Paper>
            <Typography sx={{padding:1, color: '#616975'}}>Date Added: {data.dateAdded}</Typography>
          </Paper>
        </Grid>
      </Grid>

     <Grid item xs={12} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center', color: '#1854ba'}}><Button variant='outlined' onClick={refetch}>Refetch</Button></Grid> 
    </Box>
  );
}
